.outer-body {
  background: #4e54c8;
  background: linear-gradient(to left, #8f94fb, #4e54c8);
  width: 100%;
  height: 100%;
  min-height: 100vh;
  padding: 30px;
}

.circles {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.circles li {
  position: absolute;
  display: block;
  list-style: none;
  width: 20px;
  height: 20px;
  background: rgba(255, 255, 255, 0.2);
  animation: animate 25s linear infinite;
  bottom: -150px;
}

.circles li:nth-child(1) {
  left: 25%;
  width: 80px;
  height: 80px;
  animation-delay: 0s;
}

.circles li:nth-child(2) {
  left: 10%;
  width: 20px;
  height: 20px;
  animation-delay: 2s;
  animation-duration: 6s;
}

.circles li:nth-child(3) {
  left: 70%;
  width: 20px;
  height: 20px;
  animation-delay: 4s;
}

.circles li:nth-child(4) {
  left: 40%;
  width: 60px;
  height: 60px;
  animation-delay: 0s;
  animation-duration: 8s;
}

.circles li:nth-child(5) {
  left: 95%;
  width: 50px;
  height:50px;
  animation-delay: 0s;
  animation-duration: 4s;
}

.circles li:nth-child(6) {
  left: 75%;
  width: 110px;
  height: 110px;
  animation-delay: 3s;
}

.circles li:nth-child(7) {
  left: 5%;
  width: 150px;
  height: 150px;
  animation-delay: 3s;
  animation-duration: 5s;
}

.circles li:nth-child(8) {
  left: 50%;
  width: 25px;
  height: 25px;
  animation-delay: 15s;
  animation-duration: 6s;
}

.circles li:nth-child(9) {
  left: 10%;
  width: 150px;
  height: 150px;
  animation-delay: 2s;
  animation-duration: 5s;
}

.circles li:nth-child(10) {
  left: 85%;
  width: 150px;
  height: 150px;
  animation-delay: 0s;
  animation-duration: 7s;
}

@keyframes animate {
  0% {
    transform: translateY(0) rotate(0deg);
    opacity: 1;
    border-radius: 0;
  }

  100% {
    transform: translateY(-1000px) rotate(720deg);
    opacity: 0;
    border-radius: 50%;
  }
}

.join-our-community {
  z-index: 5;
  max-width: 950px;
  margin: 0 auto;
  position: relative;
  padding: 30px;
  border-radius: 20px;
  background: #fff;
}

.join-our-community .title {
  font-weight: bold;
  padding: 10px 0;
  color: #001972;
}

.details-form .form-field {
  position: relative;
  margin: 15px 0;
}

.details-form .input-text {
  text-align: left;
  padding: 0 5px;
  display: block;
  width: 100%;
  height: 36px;
  border-width: 0 0 2px 0;
  border-color: #001972;
  font-size: 18px;
  line-height: 26px;
  font-weight: 400;
}

.details-form .input-text:focus {
  outline: none;
}

.details-form .label {
  position: absolute;
  left: 20px;
  bottom: 11px;
  font-size: 18px;
  color: #001972;
  font-size: 18px;
  line-height: 26px;
  font-weight: 400;
  cursor: text;
  text-transform: capitalize;
  transition: transform 0.2s ease-in-out;
}

.details-form .input-text + .label,
.details-form .input-text.not-empty + .label {
  transform: translateY(-20px);
  font-size: 14px;
  margin: 8px 5px;
}
